import React from "react";


class TopicPillsComponent extends React.Component {

    render() {
        return (
            <div style={{paddingLeft: "10px"}}>

                <ul className="nav nav-pills wbdv-topic-pill-list" style={{paddingTop: "10px", width: "100%"}}>
                    <li className="nav-item wbdv-topic-pill">
                        <label className="nav-link active bg-dark">Topic 1</label>
                    </li>
                    <li className="nav-item">
                        <label className="nav-link">Topic 2</label>
                    </li>
                    <li className="nav-item">
                        <label className="nav-link">Topic 3</label>
                    </li>
                    <li className="nav-item">
                        <label className="nav-link">Topic 4</label>
                    </li>
                    <li className="nav-link fa fa-plus wbdv-topic-add-btn"/>
                </ul>

            </div>
        )
    }
}

export default TopicPillsComponent